<style lang="less" scoped>
@import "../css/common.less";
.container {
  background: #fff;
  overflow: hidden;
  .header {
    flex: 0 0 auto;
    height: unit(100/@rem, rem);
    padding: 0 unit(100/@rem, rem);
    background: linear-gradient(to bottom, #5679e4, #5580e9); /* 标准的语法 */
    h1 {
      text-align: center;
      height: unit(100/@rem, rem);
      line-height: unit(100/@rem, rem);
      font-weight: 400;
      color: #fff;
    }
    z-index: 1;
  }
  .cont-wrap {
    overflow: hidden;
    flex: 1;
    .header-view {
      flex: 0 0 auto;
    }
    //按钮部分 加圆弧背景
    .my-need-assess {
      flex: 0 0 auto;
      padding-bottom: unit(25/@rem, rem);
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      position: relative;
      height: unit(140/@rem, rem);
      overflow: hidden;
      transition: all 0.3s ease-out;
      .background {
        position: absolute;
        top: unit(56/@rem, rem);
        left: 50%;
        transform: translate3d(-50%, -50%, 0);
        width: unit(960/@rem, rem);
        height: 100%;
        background: linear-gradient(to bottom, #54a0fb, #54a4ff); /* 标准的语法 */
        z-index: 1;
        border-bottom-left-radius: unit(490/@rem, rem) unit(120/@rem, rem);
        border-bottom-right-radius: unit(490/@rem, rem) unit(120/@rem, rem);
        box-shadow: 0 0 8px #3b39e0;
      }
      button {
        background: #fff;
        z-index: 1;
        padding: unit(10/@rem, rem) unit(40/@rem, rem);
        border-radius: unit(50/@rem, rem);
        color: #54a2fd;
        box-shadow: 0 0 5px #4f88e2;
      }
    }
    //疾病风险页面
    .tabItem {
      padding: 0 unit(20/@rem, rem);
      // tab栏
      .tab {
        justify-content: center;
        overflow: hidden;
        .tab-item {
          padding-top: unit(30/@rem, rem);
          padding-bottom: unit(15/@rem, rem);
          flex: 0 0 33.33%;
          text-align: center;
          color: #333;
          border-bottom: 1px solid #ccc;
          position: relative;
          //小箭头
          .arrow {
            position: absolute;
            border-color: transparent;
            border-top: 1px solid #ccc;
            border-right: 1px solid #ccc;
            height: unit(20/@rem, rem);
            width: unit(20/@rem, rem);
            bottom: -1px;
            left: 50%;
            transform: rotate(-45deg) translate3d(-80%, -2px, 0);
            background: #fff;
            z-index: 1;
          }
        }
        .active-item {
          color: #568cf0;
        }
      }
      //列表栏
      .cell {
        padding: unit(20/@rem, rem);
        background: #fff;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        .cell-item {
          flex: 1 0 unit(160/@rem, rem);
          text-align: center;
          padding: unit(15/@rem, rem) 0;
          background: #f5f5f5;
          border-radius: unit(50/@rem, rem);
          margin: unit(10/@rem, rem);
        }
      }
    }
    //评估详情
    .assess-info {
      .tab {
        flex: 0 0 auto;
        box-shadow: 0 unit(5/@rem, rem) unit(30/@rem, rem) #e7edfb;
        height: unit(90/@rem, rem);
        justify-content: space-between;
        align-items: center;
        .item {
          flex: 1;
          text-align: center;
          color: #888;
        }
        .active {
          color: #568cf0;
        }
        .line-y {
          height: unit(42/@rem, rem);
          width: 1px;
          background: #ddd;
        }
      }
    }
  }
}
</style>
<template>
  <div class="container flex">
    <!-- 头部标题栏 -->
    <header class="header">
      <h1 class="fz-36">风险评估</h1>
    </header>
    <div class="cont-wrap flex">
      <!-- 头部隐藏动画 -->
      <header-view class="header-view" ref="headerCnt" :number="67"></header-view>
      <!-- 按钮部分 加圆弧背景 -->
      <div class="my-need-assess" v-show="!isShowInfo">
        <div class="background"></div>
        <button class="fz-32" @click="isShowInfo = true">评估详情</button>
      </div>
      <!-- 疾病风险页面 -->
      <!-- activeIndex 当前tab栏显示的index的值-->
      <anyi-tab v-if="!isShowInfo" class="tabItem" :activeIndex="activeIndex">
        <div class="tab flex row" slot="tab-header">
          <div @click="activeIndex = index" class="tab-item fz-32" :class="{'active-item':index === activeIndex}" v-for="(item,index) in contentConfig" :key="item.title">
            <span>{{item.title}}</span>
            <span v-show="activeIndex === index" class="arrow"></span>
          </div>
        </div>
        <div v-show="activeIndex === index" class="cell flex row" v-for="(item, index) in contentConfig" :key="item.title">
          <span class="cell-item fz-28" v-for="subItem in item.items" :key="subItem.title">{{subItem.title}}</span>
        </div>
      </anyi-tab>

      <!-- 详情页面 -->
      <!-- 不传则不执行该操作 maskDOMObj 覆盖的DOM元素 maskHeight覆盖的DOM元素的高度 @delete 下滑删除回调 -->
      <anyi-tab @delete="deleteInfo" v-show="isShowInfo" :maskDOMObj="maskDOMObj" class="assess-info" :maskHeight="maskHeight">
        <div class="tab flex row fz-32" slot="tab-header">
          <div class="item active">
            <span class="icon"></span>
            <span class="tit">详情</span>
          </div>
          <div class="line-y"></div>
          <div class="item">
            <span class="icon"></span>
            <span class="tit">发病率</span>
          </div>
          <div class="line-y"></div>
          <div class="item">
            <span class="icon"></span>
            <span class="tit">预防</span>
          </div>
        </div>
        <div class="tab-item" key="121">
          <div v-for="item in 100" :key="item">{{item}}开始</div>
          <span>结束</span>
        </div>
      </anyi-tab>

    </div>
  </div>
</template>
<script>
import headerView from "../components/assess/header";
import anyiTab from "../components/common/anyiTab";
export default {
  components: {
    headerView,
    anyiTab
  },
  data() {
    return {
      activeIndex: 0, //tab栏当前选中值
      isShowInfo: false, //显示详情
      maskHeight: 0, //头部需要遮盖的高度
      maskDOMObj: "", //需要遮蔽的对象
      touchObj: { start: 0, move: 0, end: 0 },
      contentConfig: [
        {
          title: "疾病风险",
          items: [
            {
              title: "膀胱癌"
            },
            {
              title: "肺癌"
            },
            {
              title: "皮肤癌"
            },
            {
              title: "胃癌"
            },
            {
              title: "膀胱癌"
            },
            {
              title: "肺癌"
            },
            {
              title: "皮肤癌"
            },
            {
              title: "胃癌"
            }
          ]
        },
        {
          title: "癌症风险",
          items: [
            {
              title: "膀胱癌"
            },
            {
              title: "前列腺癌"
            },
            {
              title: "宫颈癌"
            },
            {
              title: "子宫癌"
            },
            {
              title: "肺癌"
            },
            {
              title: "皮肤癌"
            },
            {
              title: "卵巢癌"
            },
            {
              title: "乳腺癌"
            },
            {
              title: "肾癌"
            },
            {
              title: "胃癌"
            },
            {
              title: "结直肠癌"
            },
            {
              title: "胰腺癌"
            },
            {
              title: "白血病"
            },
            {
              title: "非霍奇金淋巴癌"
            }
          ]
        },
        {
          title: "家庭风险",
          items: [
            {
              title: "家庭风险"
            },
            {
              title: "意外风险"
            },
            {
              title: "健康风险"
            }
          ]
        }
      ]
    };
  },
  mounted() {
    //遮盖高度 等于头部自身高度
    this.maskHeight = this.$refs.headerCnt.$el.offsetHeight;
    this.maskDOMObj = this.$refs.headerCnt.$el;
  },
  methods: {
    //我要评估
    myNeedAssess() {
      this.isShowInfo = true;
    },
    tabClick(index) {},
    cntScroll(e) {},
    deleteInfo() {
      this.isShowInfo = false;
    }
  }
};
</script>


