
<style lang="less" scoped>
@import "../../css/common.less";
.header-top {
  position: relative;
  flex: 0 0 auto;
  height: unit(380/@rem, rem);
  overflow: hidden;
  background: linear-gradient(to bottom, #5580e9, #54a0fb); /* 标准的语法 */
  .item {
    flex: 1;
    height: unit(380/@rem, rem);
    .subItem {
      height: unit(190/@rem, rem);
      padding-top: unit(80/@rem, rem);
      text-align: center;
      align-items: center;
      .lv,
      .num {
        color: #fff;
      }
      .tit {
        color: #c6d5f9;
      }
      .icon {
        height: unit(60/@rem, rem);
        width: unit(60/@rem, rem);
        background: yellow;
      }
    }
  }
  .ball {
    flex: 0 0 auto;
    margin-top: unit(12/@rem, rem);
    width: unit(284/@rem, rem);
    justify-content: flex-start;
    align-items: center;
    position: relative;
    .txt {
      position: absolute;
      text-align: center;
      top: unit(70/@rem, rem);
      left: 50%;
      width: 100%;
      transform: translate3d(-50%, 0, 0);
      justify-content: center;
      align-items: center;
      color: #fff;
    }
    //评估标题
    .assess-tit {
      color: #b7d1fc;
      margin-top: unit(314/@rem, rem);
    }
  }
}
</style>
<template>
  <div class="header-top flex row">
    <div class="item left flex">
      <div class="subItem flex">
        <span class="lv fz-32">中风险</span>
        <span class="tit fz-28">风险等级</span>
      </div>
      <div class="subItem flex">
        <img class="icon" />
        <span class="tit fz-28">评估问题</span>
      </div>
    </div>
    <div class="item ball flex" ref="ball">
      <div>
        <canvas ref="bgcCtx"></canvas>
        <canvas ref="ctx"></canvas>
        <!-- <svg ref="svg" viewBox="0 0 90 90" class="svg" style="display: none">
                                                                              <circle ref="circleBg" r="43.5" cx="45" cy="45" fill="none" stroke-miterlimit="20" stroke-width="3" style="stroke: rgb(120, 156, 238); stroke-dasharray:  273.319, 273.319;">
                                                                              </circle>
                                                                              <circle ref="circle" r="43.5" cx="45" cy="45" fill="none" stroke-miterlimit="20" stroke-width="3" style="stroke: rgb(255, 255, 255);stroke-dasharray: 0, 273.319;">
                                                                              </circle>
                                                                          </svg> -->
        <div class="txt flex">
          <div>
            <animated-integer :value="number" class="num fz-44" />
            <span class="num fz-44">%</span>
          </div>
          <span class="fz-36 info">风险指数</span>
        </div>
      </div>
      <div class="fz-36 assess-tit">
        膀胱癌
      </div>
    </div>
    <div class="item right flex">
      <div class="subItem flex">
        <span class="num fz-32">500万></span>
        <span class="tit fz-28">风险转移</span>
      </div>
      <div class="subItem flex">
        <img class="icon" />
        <span class="tit fz-28">重新测试</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  mounted() {
    // this.initSvg();
    this.initCanvas();
  },
  props: {
    number: { type: Number, default: 0 }
  },
  watch: {
    number() {
      // this.initSvg();
      this.initCanvas();
    }
  },
  methods: {
    initCanvas() {
      //获取父盒子宽度
      var ballW = this.$refs.ball.offsetWidth;
      //背景canvas
      var cbgCtx = this.$refs.bgcCtx;
      //动态刻度canvas
      var cctx = this.$refs.ctx;

      // 获取背景的上下文
      var bgCtx = cbgCtx.getContext("2d");
      var ctx = cctx.getContext("2d");

      var len = 14; //线段长度
      var lineWidth = 2; //线段宽度
      var ratio = 10; //缩放比例
      
      //背景canvas 配置
      var bgCtxConfig = {
        ratio: ratio,
        position: {
          type: "absolute",
          top: "2%",
          left: "50%",
          rotate: "90deg"
        },
        r: ballW / 2, //半径
        height: ballW, //画布高度
        width: ballW, //画布宽度
        len: len, //线段长度
        lineWidth: lineWidth, //线段宽度
        angle: 0, //角度
        color: "#78ACF9"
      };
      var ctxCongfig = {
        ratio: ratio,
        position: {
          type: "absolute",
          top: "2%",
          left: "50%",
          rotate: "90deg"
        },
        r: ballW / 2, //半径
        height: ballW, //画布高度
        width: ballW, //画布宽度
        len: len, //线段长度
        lineWidth: lineWidth, //线段宽度
        angle: 0, //角度
        color: "#AAE942",
        colorFrom: { r: 77, g: 255, b: 255 },
        colorTo: { r: 255, g: 0, b: 0 },
        number: 13 //传入number 画百分百
      };
      var ctxInit = function(ctxObj, config) {
        if (config.ratio) {
          ctxObj.height = config.height * config.ratio;
          ctxObj.width = config.width * config.ratio;
        } else {
          ctxObj.height = config.height;
          ctxObj.width = config.width;
        }

        //传入位置参数 实现定位
        if (config.position) {
          ctxObj.style.position = config.position.type;
          ctxObj.style.left = config.position.left;
          ctxObj.style.top = config.position.top;
          ctxObj.style.transform =
            "translate3d(-50%, 0, 0)" +
            " rotate(" +
            ctxCongfig.position.rotate +
            ")";
          ctxObj.style.width = "100%";
        }
      };
      var draw = function(ctxObj, config) {
        ctxObj.clearRect(0, 0, config.r * 2, config.r * 2);
        config.angle = 0;
        var leng = config.number || 100;
        for (var i = 0; i < leng; i++) {
          ctxObj.lineWidth = config.lineWidth; //线段宽度
          //判断颜色比例
          if (config.colorFrom && config.colorTo) {
            var countRGB = function(fromRGB, ToRGB, key) {
              var count = ToRGB[key] - fromRGB[key];
              return Math.floor(count * (i + 1) / leng) + fromRGB[key];
            };
            var r = countRGB(config.colorFrom, config.colorTo, "r");
            var g = countRGB(config.colorFrom, config.colorTo, "g");
            var b = countRGB(config.colorFrom, config.colorTo, "b");
            ctxObj.strokeStyle = `rgb(${r}, ${g},${b})`;
          } else {
            ctxObj.strokeStyle = config.color || "#fff";
          }
          ctxObj.beginPath();
          ctxObj.moveTo(
            config.r + Math.cos(config.angle) * (config.r - config.len),
            config.r + Math.sin(config.angle) * (config.r - config.len)
          );
          ctxObj.lineTo(
            config.r + Math.cos(config.angle) * config.r,
            config.r + Math.sin(config.angle) * config.r
          );
          ctxObj.stroke();
          ctxObj.closePath();
          config.angle += Math.PI / 50;
        }
      };

      //设置背景canvas 宽高
      ctxInit(cbgCtx, bgCtxConfig);
      //设置动态刻度
      ctxInit(cctx, ctxCongfig);
      bgCtx.scale(bgCtxConfig.ratio, bgCtxConfig.ratio);
      ctx.scale(ctxCongfig.ratio, ctxCongfig.ratio);
      //画背景
      draw(bgCtx, bgCtxConfig);
      // //画刻度
      draw(ctx, ctxCongfig);

      //状态过渡
      var vm = this;
      function animate() {
        if (TWEEN.update()) {
          requestAnimationFrame(animate);
        }
      }
      new TWEEN.Tween({ tweeningNumber: 0 })
        .easing(TWEEN.Easing.Quadratic.Out)
        .to({ tweeningNumber: vm.number }, 2000)
        .onUpdate(function() {
          ctxCongfig.number = this.tweeningNumber.toFixed(0);
          draw(ctx, ctxCongfig);
        })
        .start();
      animate();
    },
    initSvg: function() {
      var wdt = 5;
      var config = {};
      var ballW = this.$refs.ball.offsetWidth - 1;
      var ballH = ballW;

      var svg = this.$refs.svg;
      var circleBg = this.$refs.circleBg;
      var circle = this.$refs.circle;

      config.r = ballH / 2 - wdt / 2;
      config.cx = ballH / 2;
      config.cy = ballW / 2;
      config["stroke-width"] = wdt;

      var roud = Math.PI * 2 * config.r; //周长

      svg.setAttribute("viewBox", "0 0 " + ballH + " " + ballW);
      function setAttr(target, paramObj) {
        for (var key in paramObj) {
          if (paramObj.hasOwnProperty(key)) {
            target.setAttribute(key, paramObj[key]);
          }
        }
      }
      svg.style.height = ballH + "px";
      svg.style.width = ballW + "px";
      svg.style.display = "block";
      setAttr(circleBg, config);
      circleBg.style.strokeDasharray = roud + "," + roud;
      setAttr(circle, config);
      circle.style.transformOrigin = "center";
      circle.style.transform = "rotate(90deg)";
      //状态过渡
      var vm = this;
      function animate() {
        if (TWEEN.update()) {
          requestAnimationFrame(animate);
        }
      }
      new TWEEN.Tween({ tweeningNumber: 0 })
        .easing(TWEEN.Easing.Quadratic.Out)
        .to({ tweeningNumber: vm.number * roud / 100 }, 2000)
        .onUpdate(function() {
          circle.style.strokeDasharray =
            this.tweeningNumber.toFixed(0) + "," + roud;
        })
        .start();
      animate();
    }
  }
};
</script>




