<style lang="less" scoped>
@import "../../css/common.less";
.anyi-tab {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.anyi-tab-header {
  flex: 0 0 auto;
  width: 100%;
}
.anyi-tab-item {
  width: 100%;
  flex: 1;
  position: relative;
  overflow: hidden;
}
.anyi-tab-mask {
  overflow-y: hidden;
}
.anyi-tab-item-scroll {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
</style>
<template>
  <div class="anyi-tab">
    <div class="anyi-tab-header">
      <slot name="tab-header"></slot>
    </div>
    <div @scroll.stop.prevent="itemScroll" ref="anyiTabItem" :class="{'anyi-tab-mask':isMask,'anyi-tab-item-scroll': isPositionTop}" class="anyi-tab-item" @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend">
      <transition-group :name="transitionName">
        <slot></slot>
      </transition-group>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      transitionName: "tabSlide-left", //tab栏动画
      isPositionTop: false, //是否在顶部
      isScroll: false, //是否滚动了
      bottonMax: 80,
      touchObj: { start: 0, move: 0, end: 0, temp: 0 }
    };
  },
  watch: {
    activeIndex(newValue, oldValue) {
      if (newValue > oldValue) {
        this.transitionName = "tabSlide-left";
      } else {
        this.transitionName = "tabSlide-right";
      }
    }
  },
  computed: {
    isMask() {
      return this.maskHeight && this.maskDOMObj;
    }
  },
  mounted() {
    // this.$refs.anyiTabItem.addEventListener("touchmove", e => {
    //   e.preventDefault();
    //   e.stopPropagation();
    // });
  },
  props: {
    activeIndex: {
      type: Number,
      default: 0
    },
    maskHeight: {
      type: Number,
      default: null
    },
    maskDOMObj: ""
  },
  methods: {
    touchstart(e) {
      if (!this.isPositionTop) {
        e.preventDefault();
        e.stopPropagation();
      } else {
        e.stopPropagation();
      }
      if (this.isScroll || !this.maskHeight || !this.maskDOMObj) return;

      //校验是否需要执行touch事件
      this.maskDOMObj.style.transition = "";
      this.touchObj.start = e.changedTouches[0].pageY;
    },
    touchmove(e) {
      if (!this.isPositionTop) {
        e.preventDefault();
        e.stopPropagation();
      } else {
        if (
          !this.isScroll &&
          this.isPositionTop &&
          this.touchObj.start - e.changedTouches[0].pageY < 0
        ) {
          e.preventDefault();
        }
        e.stopPropagation();
      }
      if (this.isScroll || !this.maskHeight || !this.maskDOMObj) return;

      this.touchObj.move =
        this.touchObj.start - e.changedTouches[0].pageY + this.touchObj.temp;
      if (this.touchObj.move > this.maskHeigh) {
        this.touchObj.move = this.maskHeigh;
      }
      //改变要遮挡元素的高度
      //判断方向
      this.maskDOMObj.style.height =
        this.maskHeight - this.touchObj.move + "px";

      console.log(this.touchObj.move);
    },
    touchend(e) {
      if (!this.isPositionTop) {
        e.preventDefault();
        e.stopPropagation();
      } else {
        e.stopPropagation();
      }
      if (this.isScroll || !this.maskHeight || !this.maskDOMObj) return;

      this.touchObj.temp = this.touchObj.move;
      //开启过度
      this.maskDOMObj.style.transition = "all .3s ease-in-out";
      //向上移动的时候 大于一段距离 让其吸在上面
      if (this.touchObj.temp > this.maskHeight / 2) {
        this.maskDOMObj.style.height = 0;
        console.log("在顶部了");
        this.isPositionTop = true;
        this.touchObj.temp = this.maskHeight;
        console.log(this.touchObj.temp);
      } else {
        if (this.touchObj.temp <= -this.bottonMax) {
          this.$emit("delete");
        }
        this.maskDOMObj.style.height = this.maskHeight + "px";
        //还原了
        console.log("还原了");
        this.touchObj.temp = 0;
        this.touchObj.move = 0;
        this.isPositionTop = false;
      }
    },
    //当内容滚动到头部时开启touch
    itemScroll(e) {
      e.preventDefault();
      e.stopPropagation();
      if (e.target.scrollTop === 0) {
        this.isScroll = false;
      } else {
        //滚动的时候才证明被盖住了
        this.isScroll = true;
      }
    }
  }
};
</script>





